import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Button, Table, Badge, Modal, Form, Alert, Spinner } from 'react-bootstrap';
import { FaCalendarCheck, FaPlus, FaEdit, FaEye, FaSearch, FaFilter } from 'react-icons/fa';
import { apiUrl } from '../utils/api';
import { useBranch } from '../context/BranchContext';

const Bookings = () => {
  const { selectedBranchId } = useBranch();
  const [bookings, setBookings] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [selectedBooking, setSelectedBooking] = useState(null);
  const [modalType, setModalType] = useState('add');
  const [filterStatus, setFilterStatus] = useState('All');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  // Fetch bookings from backend
  useEffect(() => {
    fetchBookings();
  }, [filterStatus, selectedBranchId]);

  const fetchBookings = async () => {
    try {
      setLoading(true);
      setError('');
      
      // Build URL with filters
      let url = '/api/bookings?limit=1000';
      if (filterStatus !== 'All') {
        url += `&status=${filterStatus}`;
      }
      if (selectedBranchId !== 'All') {
        url += `&branch_id=${selectedBranchId}`;
      }
      
      const response = await fetch(apiUrl(url));
      const data = await response.json();
      
      if (data.success && data.data && data.data.bookings) {
        // Transform backend data to match frontend format
        const transformedBookings = data.data.bookings.map(booking => ({
          id: `BK${String(booking.booking_id).padStart(3, '0')}`,
          bookingId: booking.booking_id,
          guestName: booking.guest_name,
          guestEmail: booking.guest_email,
          guestPhone: booking.guest_phone,
          hotelBranch: `SkyNest ${booking.branch_name}`,
          roomNumber: booking.room_number,
          roomType: booking.room_type,
          checkInDate: booking.check_in_date?.split('T')[0],
          checkOutDate: booking.check_out_date?.split('T')[0],
          nights: parseInt(booking.nights) || 0,
          status: booking.status,
          paymentMethod: booking.preferred_payment_method || 'N/A',
          totalAmount: parseFloat(booking.room_estimate) || 0,
          paidAmount: parseFloat(booking.advance_payment) || 0,
          discount: parseFloat(booking.discount_amount) || 0,
          lateFee: parseFloat(booking.late_fee_amount) || 0,
          bookedRate: parseFloat(booking.booked_rate) || 0,
          taxRate: parseFloat(booking.tax_rate_percent) || 0,
          bookingDate: booking.created_at?.split('T')[0],
          adults: 2, // Default values since not in database
          children: 0,
          specialRequests: ''
        }));
        
        setBookings(transformedBookings);
      } else {
        setError(data.error || 'Failed to fetch bookings');
      }
    } catch (err) {
      console.error('Error fetching bookings:', err);
      setError('Failed to connect to server. Please ensure the backend is running.');
    } finally {
      setLoading(false);
    }
  };

  const handleCreateBooking = async (newBookingData) => {
  try {
    const res = await fetch(`${API_URL}/confirmed`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        // If you use auth token later:
        // 'Authorization': `Bearer ${localStorage.getItem('token')}`
      },
      body: JSON.stringify(newBookingData)
    });

    if (!res.ok) throw new Error('Failed to create booking');
    const data = await res.json();

    setBookings(prev => [...prev, data.booking]); // add to UI
    alert(' Booking created successfully!');
  } catch (err) {
    alert(' ' + err.message);
  }
};

  const handleShowModal = (type, booking = null) => {
    setModalType(type);
    setSelectedBooking(booking);
    setShowModal(true);
  };

  const handleCloseModal = () => {
    setShowModal(false);
    setSelectedBooking(null);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'Confirmed': return 'primary';
      case 'Checked-In': return 'success';
      case 'Checked-Out': return 'info';
      case 'Pending Payment': return 'warning';
      case 'Cancelled': return 'danger';
      default: return 'secondary';
    }
  };

  const getPaymentStatus = (booking) => {
    if (booking.paidAmount >= booking.totalAmount) return 'Paid';
    if (booking.paidAmount > 0) return 'Partial';
    return 'Unpaid';
  };

  const getPaymentStatusColor = (booking) => {
    const status = getPaymentStatus(booking);
    switch (status) {
      case 'Paid': return 'success';
      case 'Partial': return 'warning';
      case 'Unpaid': return 'danger';
      default: return 'secondary';
    }
  };

  const filteredBookings = filterStatus === 'All' 
    ? bookings 
    : bookings.filter(booking => booking.status === filterStatus);

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('en-LK', {
      style: 'currency',
      currency: 'LKR'
    }).format(amount);
  };

  return (
    <Container fluid className="py-4" style={{ 
      minHeight: '100vh',
      backgroundColor: '#f8f9fa'
    }}>
      {/* Page Header */}
      <div style={{
        background: 'linear-gradient(135deg, #1a237e 0%, #0d47a1 100%)',
        color: 'white',
        padding: '30px',
        borderRadius: '12px',
        marginBottom: '30px',
        boxShadow: '0 4px 6px rgba(0,0,0,0.1)'
      }}>
        <div className="d-flex justify-content-between align-items-center">
          <div>
            <h2 style={{ margin: 0, fontSize: '2rem', fontWeight: 'bold', marginBottom: '8px' }}>Bookings Management</h2>
            <p style={{ marginBottom: 0, fontSize: '1.1rem', opacity: 0.9 }}>
              Manage hotel room bookings across all SkyNest branches
            </p>
          </div>
          <Button 
            style={{
              background: 'rgba(255, 255, 255, 0.2)',
              border: '1px solid rgba(255, 255, 255, 0.3)',
              color: 'white',
              fontWeight: '600',
              padding: '10px 24px',
              borderRadius: '8px',
              transition: 'all 0.3s ease'
            }}
            onMouseEnter={(e) => {
              e.target.style.background = 'rgba(255, 255, 255, 0.3)';
            }}
            onMouseLeave={(e) => {
              e.target.style.background = 'rgba(255, 255, 255, 0.2)';
            }}
            onClick={() => handleShowModal('add')}
          >
            <FaPlus className="me-2" />
            New Booking
          </Button>
        </div>
      </div>

      {/* Booking Statistics */}
      <Row className="mb-4">
        <Col md={3}>
          <Card style={{
            background: 'linear-gradient(135deg, #1a237e 0%, #0d47a1 100%)',
            border: 'none',
            borderRadius: '12px',
            boxShadow: '0 4px 12px rgba(26, 35, 126, 0.2)',
            color: 'white',
            height: '100%'
          }}>
            <Card.Body className="text-center">
              <h3 style={{ color: 'white', fontWeight: '700', fontSize: '2.5rem' }}>
                {bookings.length}
              </h3>
              <p style={{ color: 'rgba(255, 255, 255, 0.9)', marginBottom: 0, fontWeight: '500' }}>
                Total Bookings
              </p>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3}>
          <Card style={{
            background: 'linear-gradient(135deg, #1976d2 0%, #0d47a1 100%)',
            border: 'none',
            borderRadius: '12px',
            boxShadow: '0 4px 12px rgba(25, 118, 210, 0.2)',
            color: 'white',
            height: '100%'
          }}>
            <Card.Body className="text-center">
              <h3 style={{ color: 'white', fontWeight: '700', fontSize: '2.5rem' }}>
                {bookings.filter(b => b.status === 'Checked-In').length}
              </h3>
              <p style={{ color: 'rgba(255, 255, 255, 0.9)', marginBottom: 0, fontWeight: '500' }}>
                Current Guests
              </p>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3}>
          <Card style={{
            background: 'linear-gradient(135deg, #f59e0b 0%, #d97706 100%)',
            border: 'none',
            borderRadius: '12px',
            boxShadow: '0 4px 12px rgba(245, 158, 11, 0.2)',
            color: 'white',
            height: '100%'
          }}>
            <Card.Body className="text-center">
              <h3 style={{ color: 'white', fontWeight: '700', fontSize: '2.5rem' }}>
                {bookings.filter(b => b.status === 'Pending Payment').length}
              </h3>
              <p style={{ color: 'rgba(255, 255, 255, 0.9)', marginBottom: 0, fontWeight: '500' }}>
                Pending Payments
              </p>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3}>
          <Card style={{
            background: 'linear-gradient(135deg, #28a745 0%, #218838 100%)',
            border: 'none',
            borderRadius: '12px',
            boxShadow: '0 4px 12px rgba(40, 167, 69, 0.2)',
            color: 'white',
            height: '100%'
          }}>
            <Card.Body className="text-center">
              <h3 style={{ color: 'white', fontWeight: '700', fontSize: '2.5rem' }}>
                {formatCurrency(bookings.reduce((sum, b) => sum + b.totalAmount, 0))}
              </h3>
              <p style={{ color: 'rgba(255, 255, 255, 0.9)', marginBottom: 0, fontWeight: '500' }}>
                Total Revenue
              </p>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Filters */}
      <Row className="mb-3">
        <Col md={6}>
          <Form.Group>
            <Form.Label style={{ color: '#1a237e', fontWeight: '600', marginBottom: '8px' }}>
              Filter by Status
            </Form.Label>
            <Form.Select 
              value={filterStatus} 
              onChange={(e) => setFilterStatus(e.target.value)}
              style={{
                borderColor: '#1976d2',
                borderRadius: '8px',
                padding: '10px'
              }}
            >
              <option value="All">All Bookings</option>
              <option value="Confirmed">Confirmed</option>
              <option value="Checked-In">Checked-In</option>
              <option value="Checked-Out">Checked-Out</option>
              <option value="Pending Payment">Pending Payment</option>
              <option value="Cancelled">Cancelled</option>
            </Form.Select>
          </Form.Group>
        </Col>
      </Row>

      {/* Bookings Table */}
      <Row>
        <Col>
          <Card style={{
            background: 'white',
            borderRadius: '12px',
            border: '1px solid #e2e8f0',
            boxShadow: '0 2px 8px rgba(0,0,0,0.08)'
          }}>
            <Card.Header style={{ 
              background: 'linear-gradient(135deg, #1a237e 0%, #0d47a1 100%)',
              borderBottom: '2px solid #1976d2',
              borderRadius: '12px 12px 0 0'
            }}>
              <h5 className="mb-0" style={{ fontWeight: '700', color: 'white' }}>
                Bookings List ({filteredBookings.length})
              </h5>
            </Card.Header>
            <Card.Body style={{ padding: 0 }}>
              <div style={{ overflowX: 'auto' }}>
                <Table responsive style={{ marginBottom: 0 }}>
                  <thead style={{ backgroundColor: '#f8f9fa', borderBottom: '2px solid #1976d2' }}>
                    <tr>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Booking ID</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Guest Name</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Hotel/Room</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Check-in</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Check-out</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Nights</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Guests</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Amount</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Payment</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Status</th>
                      <th style={{ padding: '16px', fontWeight: '600', color: '#1a237e', fontSize: '0.85rem', letterSpacing: '0.5px', textTransform: 'uppercase', border: 'none' }}>Actions</th>
                    </tr>
                </thead>
                <tbody>
                  {filteredBookings.map((booking) => (
                    <tr key={booking.id}>
                      <td>
                        <strong>{booking.id}</strong>
                      </td>
                      <td>
                        <div>
                          <strong>{booking.guestName}</strong>
                          <br />
                          <small className="text-muted">{booking.guestEmail}</small>
                        </div>
                      </td>
                      <td>
                        <div>
                          <strong>{booking.hotelBranch}</strong>
                          <br />
                          <small>Room {booking.roomNumber} ({booking.roomType})</small>
                        </div>
                      </td>
                      <td>{new Date(booking.checkInDate).toLocaleDateString()}</td>
                      <td>{new Date(booking.checkOutDate).toLocaleDateString()}</td>
                      <td>{booking.nights}</td>
                      <td>
                        {booking.adults}A
                        {booking.children > 0 && `, ${booking.children}C`}
                      </td>
                      <td>{formatCurrency(booking.totalAmount)}</td>
                      <td>
                        <Badge bg={getPaymentStatusColor(booking)}>
                          {getPaymentStatus(booking)}
                        </Badge>
                        <br />
                        <small>{formatCurrency(booking.paidAmount)}</small>
                      </td>
                      <td>
                        <Badge bg={getStatusColor(booking.status)}>
                          {booking.status}
                        </Badge>
                      </td>
                      <td>
                        <div className="d-flex gap-1">
                          <Button
                            style={{
                              background: 'linear-gradient(135deg, #1a237e 0%, #0d47a1 100%)',
                              color: 'white',
                              border: 'none',
                              borderRadius: '6px',
                              padding: '6px 12px',
                              fontSize: '0.875rem',
                              transition: 'all 0.3s ease'
                            }}
                            size="sm"
                            onClick={() => handleShowModal('view', booking)}
                          >
                            <FaEye />
                          </Button>
                          <Button
                            style={{
                              background: '#6c757d',
                              color: 'white',
                              border: 'none',
                              borderRadius: '6px',
                              padding: '6px 12px',
                              fontSize: '0.875rem',
                              transition: 'all 0.3s ease'
                            }}
                            size="sm"
                            onClick={() => handleShowModal('edit', booking)}
                          >
                            <FaEdit />
                          </Button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Modal for Add/Edit/View Booking */}
      <Modal show={showModal} onHide={handleCloseModal} size="lg">
        <Modal.Header 
          closeButton
          style={{
            background: 'linear-gradient(135deg, #1a237e 0%, #0d47a1 100%)',
            color: 'white',
            borderBottom: '2px solid #1976d2'
          }}
        >
          <Modal.Title style={{ color: 'white', fontWeight: 'bold' }}>
            {modalType === 'add' && 'New Booking'}
            {modalType === 'edit' && 'Edit Booking'}
            {modalType === 'view' && 'Booking Details'}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {selectedBooking && modalType === 'view' ? (
            <Row>
              <Col md={6}>
                <h6>Guest Information</h6>
                <p><strong>Name:</strong> {selectedBooking.guestName}</p>
                <p><strong>Email:</strong> {selectedBooking.guestEmail}</p>
                <p><strong>Phone:</strong> {selectedBooking.guestPhone}</p>
                
                <h6 className="mt-4">Booking Details</h6>
                <p><strong>Booking ID:</strong> {selectedBooking.id}</p>
                <p><strong>Booking Date:</strong> {new Date(selectedBooking.bookingDate).toLocaleDateString()}</p>
                <p><strong>Status:</strong> 
                  <Badge bg={getStatusColor(selectedBooking.status)} className="ms-2">
                    {selectedBooking.status}
                  </Badge>
                </p>
              </Col>
              <Col md={6}>
                <h6>Stay Information</h6>
                <p><strong>Hotel:</strong> {selectedBooking.hotelBranch}</p>
                <p><strong>Room:</strong> {selectedBooking.roomNumber} ({selectedBooking.roomType})</p>
                <p><strong>Check-in:</strong> {new Date(selectedBooking.checkInDate).toLocaleDateString()}</p>
                <p><strong>Check-out:</strong> {new Date(selectedBooking.checkOutDate).toLocaleDateString()}</p>
                <p><strong>Nights:</strong> {selectedBooking.nights}</p>
                <p><strong>Guests:</strong> {selectedBooking.adults} Adults, {selectedBooking.children} Children</p>
                
                <h6 className="mt-4">Payment Information</h6>
                <p><strong>Total Amount:</strong> {formatCurrency(selectedBooking.totalAmount)}</p>
                <p><strong>Paid Amount:</strong> {formatCurrency(selectedBooking.paidAmount)}</p>
                <p><strong>Balance:</strong> {formatCurrency(selectedBooking.totalAmount - selectedBooking.paidAmount)}</p>
                <p><strong>Payment Method:</strong> {selectedBooking.paymentMethod}</p>
                
                {selectedBooking.specialRequests && (
                  <>
                    <h6 className="mt-4">Special Requests</h6>
                    <p>{selectedBooking.specialRequests}</p>
                  </>
                )}
              </Col>
            </Row>
          ) : (
            <Form>
              <Row>
                <Col md={6}>
                  <h6>Guest Information</h6>
                  <Form.Group className="mb-3">
                    <Form.Label>Guest Name</Form.Label>
                    <Form.Control
                      type="text"
                      defaultValue={selectedBooking?.guestName || ''}
                      placeholder="Enter guest name"
                    />
                  </Form.Group>
                  <Form.Group className="mb-3">
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                      type="email"
                      defaultValue={selectedBooking?.guestEmail || ''}
                      placeholder="Enter email address"
                    />
                  </Form.Group>
                  <Form.Group className="mb-3">
                    <Form.Label>Phone</Form.Label>
                    <Form.Control
                      type="tel"
                      defaultValue={selectedBooking?.guestPhone || ''}
                      placeholder="Enter phone number"
                    />
                  </Form.Group>
                  
                  <h6 className="mt-4">Stay Details</h6>
                  <Form.Group className="mb-3">
                    <Form.Label>Hotel Branch</Form.Label>
                    <Form.Select defaultValue={selectedBooking?.hotelBranch || ''}>
                      <option value="">Select hotel branch</option>
                      <option value="SkyNest Colombo">SkyNest Colombo</option>
                      <option value="SkyNest Kandy">SkyNest Kandy</option>
                      <option value="SkyNest Galle">SkyNest Galle</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3">
                    <Form.Label>Room Type</Form.Label>
                    <Form.Select defaultValue={selectedBooking?.roomType || ''}>
                      <option value="">Select room type</option>
                      <option value="Single">Single</option>
                      <option value="Double">Double</option>
                      <option value="Suite">Suite</option>
                    </Form.Select>
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <h6>Booking Information</h6>
                  <Form.Group className="mb-3">
                    <Form.Label>Check-in Date</Form.Label>
                    <Form.Control
                      type="date"
                      defaultValue={selectedBooking?.checkInDate || ''}
                    />
                  </Form.Group>
                  <Form.Group className="mb-3">
                    <Form.Label>Check-out Date</Form.Label>
                    <Form.Control
                      type="date"
                      defaultValue={selectedBooking?.checkOutDate || ''}
                    />
                  </Form.Group>
                  <Row>
                    <Col md={6}>
                      <Form.Group className="mb-3">
                        <Form.Label>Adults</Form.Label>
                        <Form.Control
                          type="number"
                          min="1"
                          defaultValue={selectedBooking?.adults || 1}
                        />
                      </Form.Group>
                    </Col>
                    <Col md={6}>
                      <Form.Group className="mb-3">
                        <Form.Label>Children</Form.Label>
                        <Form.Control
                          type="number"
                          min="0"
                          defaultValue={selectedBooking?.children || 0}
                        />
                      </Form.Group>
                    </Col>
                  </Row>
                  <Form.Group className="mb-3">
                    <Form.Label>Payment Method</Form.Label>
                    <Form.Select defaultValue={selectedBooking?.paymentMethod || ''}>
                      <option value="">Select payment method</option>
                      <option value="Credit Card">Credit Card</option>
                      <option value="Cash">Cash</option>
                      <option value="Bank Transfer">Bank Transfer</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3">
                    <Form.Label>Special Requests</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={3}
                      defaultValue={selectedBooking?.specialRequests || ''}
                      placeholder="Enter any special requests"
                    />
                  </Form.Group>
                </Col>
              </Row>
            </Form>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button 
            variant="secondary" 
            onClick={handleCloseModal}
            style={{
              background: '#6c757d',
              border: 'none',
              padding: '10px 24px',
              borderRadius: '6px',
              fontWeight: '500'
            }}
          >
            Close
          </Button>
          {modalType !== 'view' && (
            <Button 
              style={{
                background: 'linear-gradient(135deg, #1a237e 0%, #0d47a1 100%)',
                border: 'none',
                padding: '10px 24px',
                borderRadius: '6px',
                fontWeight: '500',
                transition: 'all 0.3s ease'
              }}
            >
              {modalType === 'add' ? 'Create Booking' : 'Save Changes'}
            </Button>
          )}
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default Bookings;